exports.push([module.id, "\nth {\n    text-align: left;\n    width: 20%;\n}\nth,\ntd {\n    padding: 20px;\n    color: #909399;\n    border-bottom: 1px solid #EBEEF5;\n}\ntr:nth-child(even) {\n    background: #FAFAFA;\n}\n.sub-title {\n    padding: 20px;\n}\n", "",{"version":3,"sources":["/usr/src/app/app/javascript/pages/User/app/javascript/pages/User/index.vue"],"names":[],"mappings":";AAmGA;IACA,gBAAA;IACA,UAAA;AACA;AAEA;;IAEA,aAAA;IACA,cAAA;IACA,gCAAA;AACA;AAEA;IACA,mBAAA;AACA;AAEA;IACA,aAAA;AACA","file":"index.vue?vue&type=style&index=0&lang=css&","sourcesContent":["<template>\n    <div>\n        <!-- Alert Message -->\n        <alert-message v-if=\"alert.type\" :alert=\"alert\" />\n        <h1></h1>\n        <hr />\n        <el-button class=\"button\" type=\"primary\" @click=\"update\"></el-button>\n        <table style=\"width: 100%;border-spacing: 0px;\">\n            <tr>\n                <th></th>\n                <td>{{user.name}}</td>\n            </tr>\n            <tr>\n                <th></th>\n                <td>{{user.email}}</td>\n            </tr>\n            <tr>\n                <th></th>\n                <td>\n                    <stripe />\n                    <div v-if=\"billing_status == 0\">\n                        <el-tag type=\"info\"></el-tag>\n                    </div>\n                    <div v-else-if=\"billing_status==1\">\n                        <el-tag type=\"success\"></el-tag>\n                    </div>\n                    <!-- <div>stripe</div> -->\n                </td>\n            </tr>\n            <tr>\n                <th></th>\n                <td>\n                    <div v-if=\"billing_status==0\">\n                        <el-tag type=\"info\"></el-tag>\n                        <div>{{last_date}}</div>\n                    </div>\n                </td>\n            </tr>\n        </table>\n        <!--  -->\n        <update-component :dialogStatus=\"dialogStatus\" :toggleModal=\"toggleModal\" :init=\"init\" :user=\"user\" />\n    </div>\n</template>\n\n<script>\nimport moment from 'moment';\nimport AlertMessage from '@/components/Shared/Alert'\nimport UpdateComponent from '@/components/User/Modal/update.vue'\nimport Stripe from '@/components/User/stripe.vue'\nvar created_date = moment([2019, 5, 20]); //\nexport default {\n    components: {\n        AlertMessage,\n        UpdateComponent,\n        Stripe\n    },\n    data() {\n        return {\n            mode: '',\n            alert: {},\n            user: {},\n            billing_status: 0,\n            created_at: created_date,\n            edit: false,\n            dialogStatus: false,\n        }\n    },\n    methods: {\n        toggleModal() {\n            this.dialogStatus = !this.dialogStatus\n        },\n        // Read\n        init() {\n            this.axios.get('/api/v1/users/' + this.$auth.user().id)\n                .then(res => {\n                    this.user = res.data.user\n                })\n                .catch(error => {\n                    this.alert = { type: error.status, message: error.message }\n                })\n        },\n        //  Create\n        update() {\n            this.toggleModal()\n        }\n    },\n    computed: {\n        last_date: function() {\n            let days = moment(this.created_at).add(60, \"days\"); //60\n            return days.diff(moment(), 'days'); //\n        }\n    },\n    mounted() {\n        this.init()\n    }\n}\n</script>\n\n<style>\nth {\n    text-align: left;\n    width: 20%;\n}\n\nth,\ntd {\n    padding: 20px;\n    color: #909399;\n    border-bottom: 1px solid #EBEEF5;\n}\n\ntr:nth-child(even) {\n    background: #FAFAFA;\n}\n\n.sub-title {\n    padding: 20px;\n}\n</style>"]}]);

