<template>
    <div>
        <!-- Alert Message -->
        <alert-message v-if="alert.type" :alert="alert" />
        <h1></h1>
        <hr />
        <el-button class="button" type="primary" @click="update"></el-button>
        <table style="width: 100%;border-spacing: 0px;">
            <tr>
                <th></th>
                <td>{{user.name}}</td>
            </tr>
            <tr>
                <th></th>
                <td>{{user.email}}</td>
            </tr>
            <tr>
                <th></th>
                <td>
                    <stripe />
                    <div v-if="billing_status == 0">
                        <el-tag type="info"></el-tag>
                    </div>
                    <div v-else-if="billing_status==1">
                        <el-tag type="success"></el-tag>
                    </div>
                    <!-- <div>stripe</div> -->
                </td>
            </tr>
            <tr>
                <th></th>
                <td>
                    <div v-if="billing_status==0">
                        <el-tag type="info"></el-tag>
                        <div>{{last_date}}</div>
                    </div>
                </td>
            </tr>
        </table>
        <!--  -->
        <update-component :dialogStatus="dialogStatus" :toggleModal="toggleModal" :init="init" :user="user" />
    </div>
</template>

<script>
import moment from 'moment';
import AlertMessage from '@/components/Shared/Alert'
import UpdateComponent from '@/components/User/Modal/update.vue'
import Stripe from '@/components/User/stripe.vue'
var created_date = moment([2019, 5, 20]); //
export default {
    components: {
        AlertMessage,
        UpdateComponent,
        Stripe
    },
    data() {
        return {
            mode: '',
            alert: {},
            user: {},
            billing_status: 0,
            created_at: created_date,
            edit: false,
            dialogStatus: false,
        }
    },
    methods: {
        toggleModal() {
            this.dialogStatus = !this.dialogStatus
        },
        // Read
        init() {
            this.axios.get('/api/v1/users/' + this.$auth.user().id)
                .then(res => {
                    this.user = res.data.user
                })
                .catch(error => {
                    this.alert = { type: error.status, message: error.message }
                })
        },
        //  Create
        update() {
            this.toggleModal()
        }
    },
    computed: {
        last_date: function() {
            let days = moment(this.created_at).add(60, "days"); //60
            return days.diff(moment(), 'days'); //
        }
    },
    mounted() {
        this.init()
    }
}
</script>

